import React from 'react';
import axios from "axios";
import TopMenu from "./common/TopMenu";
import MatchList from "./MatchList";
import "../css/main.css";

class Main extends React.Component {
  state = {
    isLoading: true,
    matchs: []    
  };
  getMatch = async () =>{
    const  {data : {result: matchs }} = await axios.get("https://dev.rankingball.com:13001/game/106001/103001/105001?type=code");
    console.log(matchs);
    this.setState({matchs, isLoading :false})         
  }
  componentDidMount(){
    this.getMatch();
  };
  render() {   
    const {isLoading, matchs} = this.state;
    return (      
        <div className="content">
          {isLoading ? (
            <div className="loader">
              <span className="loader-text">Loading...</span>
            </div>
          ) : (
            <div> 
              <TopMenu />
              <ul className="match-list">
                {matchs.map(match =>(  
                  <MatchList 
                    key={match.gi}
                    id={match.gi}   
                    gameid={match.gi} 
                    gametype={match.gt} 
                    hometeam={match.hta} 
                    awayteam={match.ata} 
                    gp={match.gp} 
                    cc={match.cc}              
                    ln={match.ln}
                  />              
                ))}                
              </ul>
            </div>            
          )}          
        </div>
      )
  }
}
export default Main;